import React, { Component, createRef } from "react";
import { Route, Switch, Redirect } from "react-router-dom";
import ReactNotification from 'react-notifications-component'
import AdminNavbar from "../components/Navbars/AdminNavbar";
// import Footer from "../components/Footer/Footer";
import Sidebar from "../components/Sidebar/Sidebar";

import routes from "../routes.js";

import sidebarImage from "../assets/img/sidebar-5.jpg";
import { getWithExpiry } from "../function";

class Admin extends Component {
  constructor(props)
  {
    super(props);
    this.state = {
      token_info: getWithExpiry('token_info'),
      image: sidebarImage,
      color: "black",
      hasImage: true,
    }

    this.mainPanel = createRef();
  }

  getRoutes = (routes) => {
    return routes.map((prop, key) => {
      if (prop.layout === "/admin") {
        var path = prop.layout + prop.path;
        if (prop.additional) path += prop.additional; 
        return (
          <Route
            path={path}
            render={(props) => <prop.component {...props} />}
            key={key}
          />
        );
      } else {
        return null;
      }
    });
  };

  componentDidMount() {
    if (!this.state.token_info) return;
    
    document.documentElement.scrollTop = 0;
    document.scrollingElement.scrollTop = 0;
    this.mainPanel.current.scrollTop = 0;
    if (
      window.innerWidth < 993 &&
      document.documentElement.className.indexOf("nav-open") !== -1
    ) {
      document.documentElement.classList.toggle("nav-open");
      var element = document.getElementById("bodyClick");
      element.parentNode.removeChild(element);
    }
  }

  render() {
    if (!this.state.token_info)
      return <Redirect to="/" />
    return (
      <div className="wrapper">
        <ReactNotification />
        <Sidebar color={this.state.color} image={this.state.hasImage ? this.state.image : ""} routes={routes} />
        <div className="main-panel" ref={this.mainPanel}>
          <AdminNavbar />
          <div className="content">
            <Switch>{this.getRoutes(routes)}</Switch>
          </div>
          {/* <Footer /> */}
        </div>
      </div>
    );
  }
}

export default Admin;
